.loader {
  margin: 0 auto;
  border: 10px solid #f3f3f3;
  border-top: 10px solid #2d2929;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
